import { useState } from "react";
import { signOut } from "firebase/auth";
import { auth, db } from '../firebase/config.js';
import { useDispatch } from 'react-redux';
import {setUser} from '../store/usersSlice.js';
import { collection, addDoc, setDoc, doc } from "firebase/firestore";
import { selectUsers } from "../store/usersSlice.js";

function MainPage() {
    const dispatch = useDispatch();
    const [userData, setUserData] = useState({});

    function handleSignOut() {
        if(window.confirm('Are you sure you want to log out?')) {
            signOut(auth).then(() => {
                dispatch(setUser(null));
            }).catch((error) => {
                console.log(error);
            });
        }
    }

    function handleUserData(e) {
        setUserData({...userData, [e.target.name]: e.target.value});
    }

    const handleAdmitData = async() => {
        try {
            const docRef = doc(db, "users", auth.currentUser.uid);
            await setDoc(docRef, {
                domainName: userData.domainName,
                productFeed: userData.productFeed,
            })
            /*const docRef = await addDoc(collection(db, "users"), {
              first: "Ada",
              last: "Lovelace",
              born: 1815
            });*/
            console.log("Document written with ID: ", docRef.id);
          } catch (e) {
            console.error("Error adding document: ", e);
          }
    }

    return (
        <div className="container">
            <div className="header-btns">

                <button onClick={handleSignOut} className="btn transparent">
                Logout
                </button>

            </div>
            <div className="form-control">
                <label>Domain name</label>
                <input onChange={(e)=>handleUserData(e)} type="text" name="domainName" placeholder="Enter your domain namel" />
            </div>
            <div className="form-control">
                <label>Product feed</label>
                <input onChange={(e)=>handleUserData(e)} type="text" name="productFeed" placeholder="Enter link to your product feed" />
            </div>
            <button onClick={handleAdmitData} className='active btn' >Admit</button>
        </div>
    )
}

export default MainPage